<template>
  <section
    id="features"
    class="pb-8"
  >
    <v-container fluid>
      <v-row
        align="center"
        justify="center"
      >
        <v-col cols="10">
          <v-card style="width: 100%">
            <h1 class="text-center pt-6 font-weight-bold">สินค้าแนะนำ</h1>
            <v-divider class="my-6"></v-divider>
            <v-row class="text-center">
              <v-col
                class="col-12 col-sm-6 col-md-4"
                v-for="product in visibleNewsProductPages"
                :key="product.idProductOfMemberMk"
              >
                <div class="flex-center">
                  <v-card-text>
                    <div class="flex-center">
                      <v-img
                        :src="product.titleImg"
                        height="250"
                        width="200"
                      ></v-img>
                    </div>
                    <div class="font-weight-bold text-uppercase my-2">
                      <h3>{{ product.nameArgi }}</h3>
                    </div>
                    <v-divider class="my-2" />
                    <div class="text-uppercase blue--text">{{ product.groupName || '-' }}</div>
                    <v-divider class="my-2" />
                    <div class="text-uppercase text-h4 mt-6 blue--text">{{ product.priceBegin === product.priceEnd ? `฿${product.priceEnd}` : 
                      `฿${product.priceBegin} - ฿${product.priceEnd}` }} / {{ product.nameUnit }}</div>
                    <v-btn
                      rounded
                      outlined
                      color="blue"
                      @click="productDetail(product.idProductOfMemberMk)"
                      class="mt-6"
                    >
                      ดูรายละเอียดเพิ่มเติม
                    </v-btn>
                  </v-card-text>
                  <v-divider
                    style="margin-right: -2px"
                    vertical
                    v-if="$vuetify.breakpoint.smAndUp"
                  ></v-divider>
                </div>
                <v-divider
                  class="mx-4"
                  v-if="!$vuetify.breakpoint.smAndUp"
                ></v-divider>
              </v-col>
            </v-row>
          </v-card>
        </v-col>
      </v-row>
      <div class="text-center mb-3">
        <v-pagination
          v-model="sugProductPage"
          :length="Math.ceil(suggestProduct.length/perPageNews)"
        ></v-pagination>
      </div>
    </v-container>
    <v-container fluid>
      <v-row
        align="center"
        justify="center"
      >
        <v-col cols="10">
          <v-card style="width: 100%">
            <h1 class="text-center pt-6 font-weight-bold">ข่าวประชาสัมพันธ์</h1>
            <v-divider class="my-6"></v-divider>
            <v-row class="text-center">
              <v-col
                class="col-12 col-sm-6 col-md-4"
                v-for="content in visibleNewsPages"
                :key="content.contentId"
              >
                <div class="flex-center">
                  <v-card-text>
                    <div class="flex-center">
                      <v-img
                        :src="content.contentImg"
                        height="250"
                        width="200"
                      ></v-img>
                    </div>
                    <div class="font-weight-bold text-uppercase my-2">
                      <h3>{{ content.contentTitle }}</h3>
                    </div>
                    <v-divider class="my-2" />
                    <v-btn
                      rounded
                      outlined
                      color="blue"
                      :to="{ name: 'Post', params: { id: content.contentId } }"
                      class="mt-6"
                    >
                      ดูรายละเอียดเพิ่มเติม
                    </v-btn>
                  </v-card-text>
                  <v-divider
                    style="margin-right: -2px"
                    vertical
                    v-if="$vuetify.breakpoint.smAndUp"
                  ></v-divider>
                </div>
                <v-divider
                  class="mx-4"
                  v-if="!$vuetify.breakpoint.smAndUp"
                ></v-divider>
              </v-col>
            </v-row>
          </v-card>
        </v-col>
      </v-row>
      <div class="text-center mb-3">
        <v-pagination
          v-model="newsPage"
          :length="Math.ceil(newsContent.length/perPage)"
        ></v-pagination>
      </div>
    </v-container>
    <v-container fluid>
      <v-row
        align="center"
        justify="center"
      >
        <v-col cols="10">
          <v-card style="width: 100%">
            <h1 class="text-center pt-6 font-weight-bold">แนะนำผลผลิต</h1>
            <v-divider class="my-6"></v-divider>
            <v-row class="text-center">
              <v-col
                class="col-12 col-sm-6 col-md-4"
                v-for="content in visiblePages"
                :key="content.contentId"
              >
                <div class="flex-center">
                  <v-card-text>
                    <div class="flex-center">
                      <v-img
                        :src="content.contentImg"
                        height="250"
                        width="200"
                      ></v-img>
                    </div>
                    <div class="font-weight-bold text-uppercase my-2">
                      <h3>{{ content.contentTitle }}</h3>
                    </div>
                    <v-divider class="my-2" />
                    <v-btn
                      rounded
                      outlined
                      color="blue"
                      :to="{ name: 'Post', params: { id: content.contentId } }"
                      class="mt-6"
                    >
                      ดูรายละเอียดเพิ่มเติม
                    </v-btn>
                  </v-card-text>
                  <v-divider
                    style="margin-right: -2px"
                    vertical
                    v-if="$vuetify.breakpoint.smAndUp"
                  ></v-divider>
                </div>
                <v-divider
                  class="mx-4"
                  v-if="!$vuetify.breakpoint.smAndUp"
                ></v-divider>
              </v-col>
            </v-row>
          </v-card>
        </v-col>
      </v-row>
      <div class="text-center mb-3">
        <v-pagination
          v-model="newsPage"
          :length="Math.ceil(contentsData.length/perPage)"
        ></v-pagination>
      </div>
    </v-container>
    <div class="svg-border-rounded text-light">
      <!-- <svg
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 144.54 17.34"
        preserveAspectRatio="none"
        fill="currentColor"
      >
        <path
          d="M144.54,17.34H0V0H144.54ZM0,0S32.36,17.34,72.27,17.34,144.54,0,144.54,0"
        />
      </svg> -->
      <svg
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 1440 320"
        preserveAspectRatio="none"
        fill="currentColor"
      >
        <path d="M0,64L80,90.7C160,117,320,171,480,181.3C640,192,800,160,960,138.7C1120,117,1280,107,1360,101.3L1440,96L1440,320L1360,320C1280,320,1120,320,960,320C800,320,640,320,480,320C320,320,160,320,80,320L0,320Z"></path>
      </svg>
    </div>
  </section>
</template>

<style scoped>
.header {
  background-color: #283e79;
  color: white;
}

.circle1 {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  background-color: #f0f8ff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.circle2 {
  border-radius: 50%;
  width: 100px;
  height: 100px;
  background-color: #e0effc;
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.svg-border-rounded svg {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  color: #f4f7f5;
  z-index: -1;
}

#features {
  z-index: 0;
}

.content {
  z-index: 1;
}

svg {
  overflow: hidden;
}

section {
  position: relative;
}
</style>

<script>
import axios from "axios";

export default {
  data: () => ({
    page: 1,
    sugProductPage: 1,
    perPageNews: 3,
    perPage: 3,
    newsPage: 1,
    planos: [
      {
        title: "Básico",
        price: "R$100,00",
        img: "f1.png",
      },
      {
        title: "Padrão",
        price: "R$150,00",
        img: "f2.png",
      },
      {
        title: "Premium",
        price: "R$250,00",
        img: "f3.png",
      },
    ],
    suggestProduct: [],
    newsContent: [],
    contentsData: [],
  }),
  computed: {
    size() {
      const size = { md: "large", xl: "x-large" }[
        this.$vuetify.breakpoint.name
      ];
      return size ? { [size]: true } : {};
    },
    visiblePages() {
      return this.contentsData.slice(
        (this.page - 1) * this.perPage,
        this.page * this.perPage
      );
    },
    visibleNewsPages() {
      return this.newsContent.slice(
        (this.newsPage - 1) * this.perPage,
        this.newsPage * this.perPage
      );
    },
    visibleNewsProductPages() {
      return this.suggestProduct.slice(
        (this.sugProductPage - 1) * this.perPageNews,
        this.sugProductPage * this.perPageNews
      );
    },
  },
  methods: {
    getContentFirstPage() {
      axios
        .get(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/content/getContentFirstPage.php"
        )
        .then((response) => {
          if (response.data.content.length > 0) {
            console.log(response.data);
            this.contentsData = response.data.content;
            this.contentsData = this.contentsData.filter((e, i) => {
              return (
                e.contentTypeTitle === "แนะนำผลผลิต" && e.contentPublish === "1"
              );
            });
            this.contentsData.map(
              (e) =>
                (e.contentImg = `https://farmtd.hrdi.or.th/farmdd/admin/upload/${e.contentImg}`)
            );
            this.contentsData.reverse();
            console.log("getContentFirstPage", this.contentsData);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    productDetail(idProduct) {
      this.$router.push({
        name: "ProductDetail",
        params: { idProductOfMemberMk: idProduct },
      });
    },
    getContent() {
      axios
        .get(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/content/getContent.php"
        )
        .then((response) => {
          if (response.data.content.length > 0) {
            this.newsContent = response.data.content.filter(
              (e) =>
                e.contentTypeTitle === "ข่าวประชาสัมพันธ์" &&
                e.contentPublish === "1"
            );
          }
          this.newsContent.map((e, i) => {
            e.contentImg = `https://farmtd.hrdi.or.th/farmdd/admin/upload/${e.contentImg}`;
          });

          this.newsContent.reverse();
          console.log("newsContent", this.newsContent);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getSuggestProduct() {
      axios
        .get(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/product/getSuggestProduct.php"
        )
        .then((res) => {
          if (res.data.suggestProduct !== 0) {
            this.suggestProduct = res.data.suggestProduct;
            console.log("suggestProduct", this.suggestProduct);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.getContent();
    this.getSuggestProduct();
    this.getContentFirstPage();
  },
};
</script>


